 var(--system-ui);\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\nheader {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  width: 100%;\n  margin-bottom: 32px;\n  padding: 16px 0px 24px 0px;\n  filter: drop-shadow(0 10px 0.75rem rgba(0, 0, 0, 0.25));\n  background-color: var(--header-background-color-1);\n  background: linear-gradient(\n    180deg,\n    var(--header-background-color-1) 10%,\n    var(--header-background-color-2) 100%\n  );\n\n  #logo {\n    width: fit-content;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 0px;\n    filter: drop-shadow(0 0 0.75rem rgba(0, 0, 0, 0.25));\n\n    img {\n      width: 280px;\n      position: relative;\n      top: 4px;\n      -webkit-user-drag: none;\n      user-select: none;\n    }\n\n    #main-title {\n      width: fit-content;\n      font-family: 'Black Ops One', var(--system-ui);\n      font-size: 4.5rem;\n      background-color: black;\n      border-radius: 16px;\n      padding: 4px 16px;\n      color: var(--logo-font-color);\n      -webkit-text-stroke: 2px var(--logo-text-strike-color);\n    }\n  }\n}\n\nmain {\n  flex: 1;\n  width: 100%;\n\n  .page[hidden='true'] {\n    display: none;\n  }\n\n  .section[hidden='true'] {\n    display: none;\n  }\n}\n\n#initial-page[hidden='false'] {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n\n  #subtitle {\n    font-size: 1.5rem;\n    font-weight: 600;\n  }\n\n  #description {\n    width: 70ch;\n    margin: 16px 0 32px 0;\n  }\n\n  .start-game-btn {\n    width: 100%;\n    display: flex;\n    gap: 16px;\n    padding: 8px 16px;\n    border: none;\n    border-radius: 8px;\n    border-bottom: 4px solid var(--button-border-color);\n    background-color: var(--button-background-color);\n\n    .text {\n      display: flex;\n      flex-direction: column;\n      align-items: flex-start;\n      letter-spacing: 0.05rem;\n      gap: 6px;\n\n      .play-vs {\n        font-size: 1rem;\n        line-height: 1rem;\n        font-weight: 400;\n      }\n\n      .opponent {\n        text-transform: uppercase;\n        font-size: 1.4rem;\n        line-height: 1.4rem;\n        font-weight: 600;\n      }\n    }\n\n    .icon {\n      width: 40px;\n    }\n\n    &:hover {\n      background-color: var(--button-hover-background-color);\n      border-bottom-color: var(--button-hover-border-color);\n      cursor: pointer;\n    }\n  }\n\n  #players-form {\n    display: flex;\n    flex-direction: column;\n    gap: 32px;\n\n    .wrapper {\n      display: flex;\n      flex-direction: column;\n      gap: 8px;\n      font-size: 1.1rem;\n    }\n\n    input {\n      border-radius: 4px;\n      border: none;\n      padding: 4px 8px;\n    }\n  }\n\n  .gamemodes {\n    display: flex;\n    flex-direction: column;\n    align-items: stretch;\n    gap: 8px;\n\n    #two-player-mode + div {\n      text-align: center;\n      text-transform: uppercase;\n      font-weight: 600;\n    }\n\n    .gamemode {\n      border: 2px solid #fff;\n      padding: 32px 64px;\n      display: flex;\n      align-items: center;\n      border-radius: 8px;\n    }\n  }\n}\n\n#gameplay-page[hidden='false'] {\n  flex: 1;\n  width: inherit;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n\n  #feedback-section {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    text-align: center;\n    gap: 8px;\n    width: 700px;\n    min-height: 80px;\n    padding: 16px;\n    color: var(--radar-content-color);\n    background-color: var(--radar-background-color);\n    border: 6px solid var(--radar-border-color);\n    border-radius: 32px;\n    font-family: 'Black Ops One', monospace, var(--system-ui);\n    font-family: 'Courier New', Courier, monospace;\n    font-weight: 600;\n    font-size: 1.1rem;\n\n    .message {\n      display: flex;\n      flex-direction: column;\n      gap: 4px;\n    }\n  }\n\n  #players-section {\n    width: inherit;\n    display: flex;\n    align-items: center;\n    flex-direction: row;\n    justify-content: center;\n    gap: 16px;\n  }\n\n  #new-game-section[hidden='false'] {\n    margin: 40px 0;\n    display: flex;\n    gap: 48px;\n\n    [type='button'] {\n      text-transform: uppercase;\n      width: 18ch;\n      padding: 8px 4px;\n      border: none;\n      border-radius: 8px;\n      border-bottom: 4px solid var(--button-border-color);\n      background-color: var(--button-background-color);\n      font-weight: 500;\n\n      &:hover {\n        background-color: var(--button-hover-background-color);\n        border-bottom-color: var(--button-hover-border-color);\n        cursor: pointer;\n      }\n    }\n  }\n}\n\n#coordinates-info-box {\n  margin-left: 40px;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  min-height: 260px;\n  min-width: 260px;\n  padding: 16px 24px;\n  border: 6px solid var(--radar-border-color);\n  background-color: var(--radar-background-color);\n  border-radius: 32px;\n  gap: 8px;\n\n  .title,\n  .title + div {\n    font-family: 'Courier New', Courier, monospace;\n    color: var(--radar-content-color);\n    font-weight: 600;\n    text-transform: uppercase;\n    padding: 4px 8px;\n    width: 100%;\n    text-align: center;\n    font-size: 0.9rem;\n  }\n\n  .title {\n    border: 2px solid var(--radar-content-color);\n  }\n\n  .wrapper {\n    display: flex;\n    gap: 16px;\n    align-items: center;\n    justify-content: center;\n  }\n\n  .missile-box {\n    width: 100px;\n    user-select: none;\n    padding: 16px;\n\n    .icon {\n      -webkit-user-drag: none;\n    }\n\n    .icon[face-to='left'] {\n      transform: rotate(335deg);\n    }\n\n    .icon[face-to='right'] {\n      transform: rotate(115deg);\n    }\n  }\n\n  .coordinates {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    text-align: center;\n    font-size: 1.2rem;\n    width: 70px;\n    height: 70px;\n    padding: 16px;\n    font-family: monospace, var(--system-ui);\n    font-weight: 600;\n    color: var(--radar-content-color);\n    border: 3px dashed currentColor;\n    border-radius: 16px;\n  }\n}\n\n.player-box {\n  padding: 16px;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n\n  .title {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    gap: 8px;\n    text-align: center;\n    margin: 16px 0;\n    font-size: 1.2rem;\n    font-weight: 600;\n\n    .target {\n      width: 30px;\n      height: 30px;\n      margin: 0;\n\n      img.hidden {\n        display: none;\n      }\n    }\n  }\n\n  .grid {\n    display: grid;\n    grid-template-columns: repeat(11, var(--square-size));\n    grid-template-rows: repeat(11, var(--square-size));\n\n    & > * {\n      align-items: center;\n      justify-items: center;\n    }\n  }\n\n  .indexes {\n    display: grid;\n    font-weight: 600;\n\n    &.x-indexes {\n      grid-template-columns: subgrid;\n      grid-column: 2 / -1;\n      grid-row: 1 / span 1;\n    }\n\n    &.y-indexes {\n      grid-template-rows: subgrid;\n      grid-column: 1 / span 1;\n      grid-row: 2 / -1;\n    }\n  }\n\n  .board {\n    display: grid;\n    grid-column: 2 / -1;\n    grid-row: 2 / -1;\n    grid-template-columns: subgrid;\n    grid-template-rows: subgrid;\n    outline: 6px solid var(--board-border-color);\n    background-image: url('../assets/imgs/dark-ocean-background.png');\n    background-size: cover;\n\n    .square {\n      border: 1px solid var(--board-border-color);\n      width: 100%;\n      height: 100%;\n      display: flex;\n      align-items: center;\n      justify-content: center;\n\n      .attack-mark {\n        width: 50%;\n        height: 50%;\n        border-radius: 50%;\n        background-color: transparent;\n      }\n    }\n\n    .square[ship='false'] {\n      background-color: var(--square-background-color);\n    }\n\n    .square[ship='true'] {\n      background-color: var(--square-ship-background-color);\n    }\n  }\n\n  .board.on-ship-placement {\n    .square[preview='allowed'] {\n      border: 4px solid var(--square-allowed-border-color);\n      cursor: pointer;\n    }\n\n    .square[preview='not-allowed'] {\n      border: 4px solid var(--square-not-allowed-border-color);\n      cursor: not-allowed;\n    }\n  }\n\n  .board.on-play {\n    .attack-mark {\n      transition: background-color 0.5s ease;\n    }\n\n    .square[ship='false'][attacked='true'] .attack-mark {\n      background-color: var(--attack-mark-miss-color);\n    }\n\n    .square[ship='true'][attacked='true'] .attack-mark {\n      background-color: var(--attack-mark-hit-color);\n    }\n\n    &.hidden-fleet {\n      .square[ship='true'] {\n        background-color: var(--square-background-color);\n      }\n    }\n\n    &.targetable {\n      .square:hover {\n        border: 2px solid var(--square-hover-border-color);\n        outline: 2px solid var(--square-hover-border-color);\n        z-index: 1;\n      }\n\n      .square[attacked='false'] {\n        cursor: crosshair;\n      }\n\n      .square[attacked='true'] {\n        cursor: not-allowed;\n      }\n    }\n\n    &:not(.targetable) {\n      opacity: 60%;\n\n      .square {\n        cursor: not-allowed;\n      }\n    }\n  }\n}\n\nbutton.confirm-fleet {\n  margin: 30px 0px 20px 40px;\n  text-transform: uppercase;\n  padding: 8px 32px;\n  border: none;\n  border-radius: 8px;\n  border-bottom: 4px solid var(--button-border-color);\n  background-color: var(--button-background-color);\n  font-weight: 500;\n  font-size: 1rem;\n\n  &:hover {\n    background-color: var(--button-hover-background-color);\n    border-bottom-color: var(--button-hover-border-color);\n    cursor: pointer;\n  }\n}\n\nspan.shift-key {\n  border: 1px solid currentColor;\n  border-radius: 2px;\n  padding: 2px 4px;\n  font-size: 0.9em;\n}\n\nfooter {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 2px 0;\n  margin: 20px 0 4px 0;\n  width: 100%;\n\n  a {\n    text-decoration: none;\n    color: inherit;\n  }\n}\n"],"sourceRoot":""}]);
